import React from 'react'
import TextField from '@mui/material/TextField';
import { IoMdArrowBack } from "react-icons/io";
import Image from 'next/image';
import Link from 'next/link';
import { redirect } from 'next/navigation';
import axios from 'axios';

// Server Action para manejar el login
async function handleLogin(formData: FormData) {
  'use server'
  
  const email = formData.get('email') as string;
  const password = formData.get('password') as string;
  
  try {
    // Aquí harías la llamada a tu API
    const response = await axios.post(`${process.env.NEXT_PUBLIC_API_URL}/Account/login`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      data: { email, password },
    });
    
    if (response.status === 200) {
      redirect('/Dashboard');
    } else {
      redirect('/Login?error=invalid_credentials');
    }
  } catch (error) {
    redirect('/Login?error=server_error');
  }
}

const Login = () => {
  return (
    <div className="relative bg-white w-[900px] h-[600px] flex flex-col items-center justify-center rounded-lg shadow-lg p-6 mx-auto mt-[100px]">
      <Link href="/" className="absolute left-20 top-6 text-[#113931] w-[30px] h-[30px] cursor-pointer">
        <IoMdArrowBack />
      </Link>
      
      <div className="flex flex-col items-center justify-center mb-6">
        <div className="mb-10">
          <h1 className="text-[#113931] text-3xl font-bold">Login</h1>
        </div>

        <div className="flex flex-col items-center justify-center gap-y-2">
          <form action={handleLogin} className='flex flex-col items-center justify-center gap-y-4'>
            <TextField 
              id="email" 
              label="Correo electrónico" 
              variant="standard" 
              type="email" 
              name="email" 
              required
            />
            <TextField 
              id="password" 
              label="Contraseña" 
              variant="standard" 
              type="password" 
              name="password" 
              required
            />
            <button 
              type="submit"
              className="bg-[rgb(31,133,119)] w-[200px] h-[50px] text-xl rounded-lg text-white font-medium cursor-pointer hover:opacity-80"
            >
              Iniciar sesión
            </button>
            <p>
              ¿No tienes una cuenta? <Link href="/Signup" className="text-[rgb(31,133,119)] cursor-pointer">Regístrate</Link>
            </p>
          </form>
        </div>
      </div>
    </div>
  )
}

export default Login